import React from 'react';
import '../Items/Items.css';

class Items extends React.Component{
    constructor(props){
        super(props);
    }
    render() {
        let getFullData = this.props.items;
        return(
            getFullData.items.map(item => {
                return(
                    <div className="item" key={item.login.md5}>
                        <div>
                            <img src={item.picture.large} className="photo"></img>
                        </div>
                        <h2 className="full-name">Name: {item.name.first.charAt(0).toUpperCase() + item.name.first.slice(1)}</h2>
                        <h3 className="user-age">Age: {item.dob.age} years old</h3>
                    </div>
                )
            })
        );
    }
}

export default Items;